.posts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 155px;
  @include maxmedia(1280px) {
    padding-bottom: 60px;
  };

  &__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    margin-bottom: 110px;
    @include maxmedia(1280px) {
      flex-wrap: wrap;
    };
    @include maxmedia(1280px) {
      margin-bottom: 30px;
    };
  }

  &__column {
    @include maxmedia(1280px) {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: flex-start;
      width: 100%;
      margin-bottom: 20px;
    };
  }

  &__item {
    width: 374px;
    margin-bottom: 30px;
    border-bottom: 3px solid #333;
    @include maxmedia(992px) {
      width: 45%;
    };

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;

    &--small {
      padding: 8px 0;
      justify-content: space-between;
      border-top: 1px solid #dbdbdb;
    }
  }

  &__title {
    margin: 0 0 0 8px;
    @include maxmedia(640px) {
      font-size: 12px;
    };
  }

  &__text {
    @include maxmedia(640px) {
      font-size: rem(12);
    };
  }

  &__link {
    color: #828282;
    font-family: $font-family--lato;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    @include maxmedia(640px) {
      font-size: 10px;
    };
  }

  &__date {
    color: #828282;
    font-family: $font-family--lato;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    @include maxmedia(640px) {
      font-size: 10px;
    };
  }

  &__btn {
    padding-left: 83px;
    padding-right: 83px;
  }
}