.form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  margin: auto;
  padding-bottom: 70px;
  @include maxmedia(768px) {
    width: 100%;
  };

  &__title {
    margin: 0;
    color: $white;
    padding: 16px 190px 16px 16px;
    font-size: rem(18);
    font-weight: 700;
    background-color: #333;
    @include maxmedia(640px) {
      width: 100%;
      padding: 10px;
      text-align: center;
    };

    span {
      margin-left: 6px;
      color: #f1ad1d;
    }
  }

  &__text {
    font-family: $font-family--lato;
    font-weight: 400;
    text-align: center;
  }

  &__box {
    width: 100%;
  }

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    width: 100%;
    margin-bottom: 20px;
  }

  &__label {
    display: inline-block;
    width: 45%;
    @include maxmedia(640px) {
      width: 100%;
      margin-bottom: 10px;
    };

    &--textarea {
      display: block;
      width: 100%;
      margin-top: 20px;
    }

    &.has-error {
      .form__input {
        border: 1px solid red;
      }

      .form__textarea {
        border: 1px solid red;
      }

      .help-block {
        color: red;
        font-family: $font-family--lato;
        font-weight: 400;
      }
    }
  }

  &__input {
    display: block;
    width: 100%;
    padding: 12px;
    border: 1px solid #333;
    @include maxmedia(640px) {
      padding: 18px;
    };

    &:focus {
      border: 1px solid #f1ad1d;
      background-color: $white;
      outline: none;
    }
  }

  &__textarea {
    width: 100%;
    min-height: 185px;
    padding: 12px;
    border: 1px solid #333;
    background-color: transparent;
    @include maxmedia(640px) {
      padding: 18px;
    };

    &:focus {
      border: 1px solid #f1ad1d;
      background-color: $white;
      outline: none;
    }
  }

  &__btn {
    padding: 8px 35px;
    text-transform: uppercase;
    @include maxmedia(768px) {
      padding: 16px 80px;
    };
    @include maxmedia(768px) {
      width: 48%;
      padding: 16px 80px;
    };
    @include maxmedia(640px) {
      width: 100%!important;
      margin-bottom: 20px;
    };

    &--reset {
      margin-right: 20px;
    }
  }
}