.team {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 1px;
  padding-bottom: 165px;
  @include maxmedia(1280px) {
    padding-bottom: 60px;
  };

  &__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    @include maxmedia(768px) {
      justify-content: flex-start;
    };
  }

  &__item {
    display: flex;
    align-items: flex-end;
    width: 16.6%;
    height: 180px;
    padding: 15px;
    background-color: #000;
    border: 1px solid $white;
    @include maxmedia(768px) {
      width: 25%;
      text-align: center;
    };
    @include maxmedia(640px) {
      width: 50%!important;
    };
  }

  &__link {
    display: inline-block;
    width: 100%;
    padding: 8px 13px;
    font-family: $font-family--lato;
    font-weight: 400;
    background-color: #333;

    span {
      margin-left: 3px;
      color: #f1ad1d;
    }
  }
}