.works {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  &__title {
    @include maxmedia(768px) {
      padding: 0 50px;
      text-align: center;
    };
  }

  &__catalog {
    width: 100%;
  }

  &__controls {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    max-width: 835px;
    margin: auto auto 47px;
    @include maxmedia(768px) {
      justify-content: center;
    }
  }

  &__btn {
    margin-bottom: 20px;
    padding: 8px 40px;
    font-family: $font-family--lato;
    font-weight: 400;
    line-height: 19px;
    border: 1px solid #333;
    transition: $default-transition;
    @include maxmedia(768px) {
      margin-right: 10px;
    }

    &:hover {
      color: #f1ad1d;
      background-color: #000;
    }
  }

  &__list {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    text-align: justify;
    font-size: 0;
    @include maxmedia(992px) {
      justify-content: space-around;
      margin-bottom: 60px;
    };
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 280px;
    height: 270px;
    margin-bottom: 15px;
    padding: 35px 20px;
    border: 2px solid #333;
    background-color: $white;

    &::after {
      position: absolute;
      top: 0;
      left: 0;
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      background-color: $black;
      z-index: 2;
      transition: $default-transition;
      @include maxmedia(992px) {
        display: none;
      };
    }

    &.active {
      &::after {
        opacity: 0;
        z-index: -1;
        @include maxmedia(992px) {
          display: none;
        };
      }
    }

    &.animated {
      &::after {
        opacity: 0;
        z-index: -1;
      }
    }
  }

  &__title {
    &--small {
      position: relative;
      margin: 0;
      font-family: $font-family--lato;
      font-size: rem(18);
      font-weight: 400;

      &::after {
        position: absolute;
        bottom: -20px;
        left: 50%;
        content: '';
        display: block;
        width: 60px;
        height: 3px;
        margin-left: -30px;
        background-color: #f48e0b;
        @include maxmedia(768px) {
          bottom: -10px;
        }
      }
    }
  }

  &__text {
    &--small {
      font-family: $font-family--lato;
      font-size: rem(14);
      font-weight: 400;
      text-align: center;
    }
  }

  .btn {
    border: none;
  }

  &__gap {
    width: 280px;
  }
}