.nav {
  &__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0;
    list-style: none;
  }

  &__item {
    margin-right: rem(20);

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    position: relative;
    display: block;

    &:hover {
      color: $link-color;

      &::after {
        position: absolute;
        top: 0;
        left: 0;
        content: '';
        display: block;
        width: 100%;
        height: rem(5);
        background-color: $link-color--hover;
        transition: $default-transition;
      }
    }
  }
}
