.btn {
  display: inline-block;
  padding: rem(12) rem(27);
  font-family: $font-family--lato;
  color: $link-color;
  font-size: rem(14);
  font-weight: 700;
  text-align: center;
  background-color: $btn-bg;
  transition: $default-transition;

  &:hover {
    color: $link-color;
    background-color: $link-color--hover;
  }

  &:active,
  &:focus {
    color: $link-color;
    background-color: $btn-bg;
    opacity: 0.7;
  }
}