// Базовые цвета https://colorscheme.ru/color-converter.html
$black: hsl(0, 0%, 0%);
$white: hsl(0, 0%, 100%);
$color-main: hsl(208, 98%, 43%);
$color-success: hsl(120, 39%, 54%);
$color-danger: hsl(2, 64%, 58%);
$color-warning: hsl(35, 84%, 62%); // Семантические цвета
$text-color: #505050;
$body-bg: #fff;
$link-color: #fff;
$link-color--hover: #54d154;
$btn-bg: #f1ad1d; // Базовая типографика
$font-size--root: 15;
$font-size: rem(14);
$line-height: 1.5;
$font-family--custom: 'Montserrat', 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
$font-family: 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol'; // Ширины
//$font-family--headings:       $font-family;
//$font-family--monospace:      SFMono-Regular, 'Menlo', 'Monaco', 'Consolas', 'Liberation Mono', 'Courier New', monospace;
//$font-family--serif:          'Georgia', 'Times New Roman', 'Times', serif;
$font-family--lato: 'Lato', $font-family--custom;
$max-width: 1170px;
$min-width: 320px;
$screen-xs: 0;
$screen-sm: 480px;
$screen-md: 768px;
$screen-lg: 992px;
$screen-xl: 1200px;
$screen-xxl: 1800px;
$container-sm: 100%;
$container-md: 100%;
$container-lg: $screen-lg - 30;
$container-xl: $screen-xl - 30;
$container-xxl: $screen-xxl - 30; // Модульная сетка
$grid-columns: 12;
$grid-gutter-width: rem(30); // Разное
$default-transition: all 0.2s linear;