.features {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 35px;

  &__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
    width: 100%;
    @include maxmedia(768px) {
      flex-wrap: wrap;
    };
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 270px;
    padding-bottom: 35px;
    border-bottom: 3px solid #333;
    @include maxmedia(768px) {
      width: 50%;
      border-bottom: none;
    };
  }

  &__title {
    &--big {
      width: 100%;
      padding: 15px 0;
      color: $white;
      font-size: rem(18);
      line-height: rem(18);
      font-weight: 700;
      text-align: center;
      background-color: #333;
      @include maxmedia(768px) {
        font-size: rem(14);
      };
    }

    span {
      margin-left: 5px;
      color: #f1ad1d;
      vertical-align: top;
    }

    &--small {
      margin: 0 0 8px;
      font-family: $font-family--lato;
      font-size: rem(18);
      font-weight: 400;
      @include maxmedia(640px) {
        text-align: center;
      };
    }
  }

  &__text {
    position: relative;
    padding-bottom: 35px;
    font-family: $font-family--lato;
    font-weight: 400;
    text-align: center;

    &::after {
      position: absolute;
      bottom: 0;
      left: 50%;
      content: '';
      display: block;
      width: 60px;
      height: 4px;
      margin-left: -30px;
      background-color: #f48e0b;
    }

    &--small {
      margin: 0;
      padding: 0;
      font-family: $font-family--lato;
      font-size: rem(14);
      font-weight: 400;
      line-height: 20px;

      &::after {
        display: none;
      }
    }
  }
}