@at-root {
  @-ms-viewport {
    width: device-width;
  }
}

html {
  font-family: $font-family;

  &.fonts-loaded {
    font-family: $font-family--custom;
  }
}

.page {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%;
  color: $text-color;
  font-size: $font-size;
  line-height: $line-height;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

body {
  // Типографика проекта — в блоке typo
  display: flex;
  // Исправляем баг в IE для min-height and flexbox (flex-direction:column)
  flex-direction: column;
  // и прижимаем footer в IE 10-11
  background-color: #fff;
  margin: 0;
  min-height: 100%;
}

[tabindex='-1']:focus {
  outline: none !important;
}
// Коррекция для кликабельных элементов с указанной ролью в iOS

[role='button'] {
  cursor: pointer;
}
// Мы любим Сафари (на самом деле, нет).

input[type='search'] {
  -webkit-appearance: textfield;
}
// area,
// summary,

[role='button'],
input,
label,
select,
textarea,
a,
button {
  touch-action: manipulation; // Стилевая коррекция 300ms задержки (для поддерживающих браузеров)
  margin: 0;
  // Убираем margin в Firefox и Safari
}

button,
input {
  overflow: visible;
  // Покажем торчащее в Edge
  text-transform: none; // Уберём наследование text transform в Firefox
  border: none;
  background: none;
  outline: none;
}

button {
  padding: 0;
  cursor: pointer;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; // Коррекция проблем стилизации для Safari
}

[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox; // Коррекция для Mobile Safari (https://github.com/twbs/bootstrap/issues/11266)
}

textarea {
  overflow: auto;
  // Удаление верт. скролла в IE.
  resize: vertical; // Правильное изменение размеров
}

svg {
  a &,
  button & {
    pointer-events: none; // Мы любим Edge, это браузер! (почти)
  }

  &:not(:root) {
    overflow: hidden; // Скрыть выступающее за границы в IE
  }
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: $link-color;
  text-decoration: none;
  background-color: transparent;
  text-decoration-skip: ink;
  -webkit-text-decoration-skip: objects;
  transition: $default-transition;

  &:focus,
  &:hover {
    color: $link-color--hover;
  }
}

hr {
  box-sizing: content-box; // Проблема Firefox
  overflow: visible;
  // Проблема Edge и IE
}

li,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

::-webkit-input-placeholder {
  font-family: $font-family--lato;
  font-size: rem(14);
  line-height: 12px;
  font-weight: 400;
}

::-moz-placeholder {
  font-family: $font-family--lato;
  font-size: rem(14);
  line-height: 12px;
  font-weight: 400;
}

:-moz-placeholder {
  font-family: $font-family--lato;
  font-size: rem(14);
  line-height: 12px;
  font-weight: 400;
}

:-ms-input-placeholder {
  font-family: $font-family--lato;
  font-size: rem(14);
  line-height: 12px;
  font-weight: 400;
}