.title {
  margin: 80px 0 0;
  color: #333;
  font-size: rem(22);
  font-weight: 700;
  text-transform: uppercase;
  @include maxmedia(1280px) {
    margin: 50px 0 0;
  };
  @include maxmedia(640px) {
    text-align: center;
  };

  &__text {
    position: relative;
    margin-bottom: 45px;
    padding-bottom: 50px;
    font-family: Lato, $font-family--custom;
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
    @include maxmedia(1280px) {
      padding: 0 50px 50px;
      text-align: center;
    };
    @include maxmedia(1280px) {
      padding: 0 20px 20px;
    };

    &::after {
      position: absolute;
      bottom: 0;
      left: 50%;
      content: '';
      display: block;
      width: 60px;
      height: 4px;
      margin-left: -30px;
      background-color: #f48e0b;
    }
  }

  &--ideas {
    position: relative;
    color: $white;
    text-align: center;
    @include maxmedia(640px) {
      margin: 0;

      span {
        display: block;
      }
    };

    span {
      margin: 0 5px;
      color: #f1ad1d;
    }

    &::after {
      position: absolute;
      bottom: -50px;
      left: 50%;
      content: '';
      display: block;
      width: 60px;
      height: 4px;
      margin-left: -30px;
      background-color: #f1ad1d;
      @include maxmedia(640px) {
        bottom: -20px;
      };
    }
  }
}