.tab {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  @include maxmedia(768px) {
    flex-direction: column;
  };

  &__caption {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 55%;
    @include maxmedia(1280px) {
      justify-content: flex-start;
    };
    @include maxmedia(768px) {
      flex-wrap: nowrap;
      width: 100%;
    };
    @include maxmedia(400px) {
      flex-wrap: wrap;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    width: 270px;
    height: 148px;
    padding: 15px;
    margin-bottom: 20px;
    background-color: #000;
    cursor: pointer;
    @include maxmedia(1280px) {
      width: 170px;
      height: 100px;
    };
    @include maxmedia(768px) {
      justify-content: center;
      align-items: center;
      width: 25%;
      height: 60px;
      border: 3px solid $white;
    };
    @include maxmedia(640px) {
      padding: 0;
    };
    @include maxmedia(400px) {
      width: 50%;
    }

    &:nth-child(1),
    &:nth-child(3) {
      margin-right: 30px;
      @include maxmedia(768px) {
        margin-right: 0;
      };
    }
  }

  &__link {
    display: inline-block;
    padding: 10px 17px;
    font-family: $font-family--lato;
    color: $white;
    font-size: rem(14);
    line-height: 12px;
    font-weight: 400;
    background-color: #111;
    @include maxmedia(640px) {
      padding: 5px;
      font-size: rem(10);
      text-transform: uppercase;
    };
  }

  &__container {
    width: 43%;
    padding-left: 30px;
    @include maxmedia(768px) {
      width: 100%;
      padding-left: 0;
    };
  }

  &__content {
    display: none;
  }

  &__content.active {
    display: block;
  }

  &__title {
    margin: 0;
    color: #333;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;

    span {
      margin-left: 5px;
      color: #f1ad1d;
    }
  }

  &__text {
    font-family: $font-family--lato;
    line-height: 22px;
    font-weight: 400;
  }

  &__btn {
    margin-top: 30px;
  }
}