.animTarget{
	display: none;
}

.fa-enter{
	visibility: hidden;
}

.fa-enter-active{
	animation: niceIn 0.5s linear;
}

.fa-enter-to{

}

.fa-leave{

}

.fa-leave-active{
	animation: niceOut 0.5s linear forwards;
}

.fa-leave-to{

}

@keyframes niceIn{
	from{
		transform: translateX(-100px) scale(0.9);
		opacity: 0;
	}
	to{
		transform: translateX(0px) scale(1);
		opacity: 1;
	}
}

@keyframes niceOut{
	from{
		transform: translateX(0px) scale(1);
		opacity: 1;
	}
	to{
		transform: translateX(100px) scale(0.9);
		opacity: 0;
	}
}
