.footer {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;

	&__map {
		width: 100%;
		height: 65px;
		padding-top: 20px;
		background-color: #333;
		text-align: center;
	}

	&__text {
		padding: 10px 0;
		font-family: $font-family--lato;
		font-weight: 400;
	}
}