.header {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  height: 100vh;
  padding-left: 85px;
  padding-bottom: 145px;
  background-color: #000;
  @include maxmedia(1280px) {
    padding: 0 20px 20px;
  };
  @include maxmedia(640px) {
    height: 300px;
  };

  &__title {
    margin: 0;
    animation-delay: 1s;
  }

  &__text {
    display: inline-block;
    padding: 32px;
    font-size: rem(54);
    text-transform: uppercase;
    color: $white;
    font-weight: 700;
    background-color: $black;
    border: 1px solid $white;
    @include maxmedia(1280px) {
      padding: 26px;
      font-size: rem(32);
    };
    @include maxmedia(640px) {
      padding: 10px;
      font-size: rem(14);
    };

    &--white {
      color: #f48e0b;
      background-color: $white;
    }
  }
}